import React from 'react';
import mainComponentObjects from './mainComponentObjects.js';

const Card = ({index,name}) =>{
	return(
			<div className='cardComponent grow'>
				<a className="folder_item category">
					{mainComponentObjects[index].image}
					<span className="item_description">{name}</span>	
				</a>
			</div>
		)
}
export default Card


