import React from 'react';

const SideNav =({props})=>{
	
		return (
		<div id='sideNavContainer'>
		  <span id='open' onClick={props}>&#9776; Kategorie produktw</span>
		  <div id="mySidenav" className='sidenav'>
		    <a onClick={props} id='close' className='shadow-hover'>&times;</a>
		    <a className='shadow-hover' >Odbojniki, stopki samoprzylepne</a>
		    <a className='shadow-hover' >Tamy, wykroje samoprzylepne</a>
		    <a className='shadow-hover' >Akcesoria wiatowodowe</a>
		    <a className='shadow-hover' >Ferryty / rdzenie ferrytowe</a>
		    <a className='shadow-hover' >Tulejki dystansowe</a>
		    <a className='shadow-hover' >Prowadnice, mocowania do PCB</a>
		    <a className='shadow-hover' >Nity, zatrzaski, zalepki</a>
		    <a className='shadow-hover' >Plastikowe wkrty, rubki, nakrtki, podkadki</a>
		    <a className='shadow-hover' >Metalowe rubki z podkadk</a>
		    <a className='shadow-hover' >Akcesoria, uchwyty, osony kabli</a>
		    <a className='shadow-hover' >Korytka kablowe</a>
		    <a className='shadow-hover' >Koszulki termokurczliwe</a>
		    <a className='shadow-hover' >Przelotki, przepusty kablowe</a>
		    <a className='shadow-hover' >Dawnice kablowe</a>
		    <a className='shadow-hover' >Osony krawdzi, naronikw</a>
		    <a className='shadow-hover' >Opaski zaciskowe</a>
		    <a className='shadow-hover' >Uchwyty do opasek kablowych</a>
		    <a className='shadow-hover' >Filtry, osony do wentylatorw</a>
		    <a className='shadow-hover' >Podstawki i oprawki pod LED</a>
		    <a className='shadow-hover' >Tulejki, izolatory, osony</a>
		    <a className='shadow-hover' >Amortyzatory, reduktory wibracji</a>
		    <a className='shadow-hover' >Stopki, nki do obudw</a>
		    <a className='shadow-hover' >Stopki meblowe i przemysowe</a>
		    <a className='shadow-hover' >Podkadki samoprzylepne</a>
		    <a className='shadow-hover' >Pokrta i uchwyty</a>
		    <a className='shadow-hover' >Zalepki, uchwyty do szka</a>
		    <a className='shadow-hover' >Zalepki i nasadki</a>
		    <a className='shadow-hover' >Wkadki, korki, zalepki do rur</a>
		    <a className='shadow-hover' >Kocwki, nakadki na rury</a>
		    <a className='shadow-hover' >Osony rur i konierzy</a>
		    <a className='shadow-hover' >Elementy maskujce odporne na wysokie temperatury</a>
		    <a className='shadow-hover' >Osony zabezpieczajce</a>
		    <a className='shadow-hover' >Zabezpieczenia: budowlane, siatki</a>
		    <a className='shadow-hover' >Kka meblowe</a>
		    <a className='shadow-hover' >Akcesoria meblowe, tapicerskie</a>
		    <a className='shadow-hover' >Uszczelki</a>
		    <a className='shadow-hover' >Narzdzia</a>
		    <a className='shadow-hover' >Metalowe elementy zczne</a>
		    <a className='shadow-hover' >Uchwyty szafek, meblowe</a>
		    <a className='shadow-hover' >Kapturki i osony na ruby i nity</a>
		    <a className='shadow-hover' >Przyssawki</a>
		    <a className='shadow-hover' >Spryny</a>
		  </div>

		  
		</div>
		)
	}

export default SideNav


