import React, { Component } from 'react';
import './App.css';
import Baner from './baner.js';
import SideNav from './sidenav.js';
import Scroll from './scroll.js';
import Main from './main.js';
import Ofirmie from './ofirmie';
import ProjektyIndywidualne from './projektyIndywidualne.js';
import Wspolpraca from './wspolpraca';


class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      searchField:"",
      whatToRender: 'card'
    }
  }

  changeWidth=()=>{
    const scrol = document.querySelector('#scrol');
    const side = document.querySelector('.sidenav');
    if(document.body.clientWidth<571){
      side.style.width='100%';
      side.style.marginTop='1100px';
      
      return scrol.style.flexDirection='column';
    }
    side.style.marginTop='initial';
    side.style.width='initial';
    return scrol.style.flexDirection='row';
  }
  onclick = (event)=>{
    const main = document.querySelector('.mainComponent');
    const side = document.querySelector('.sidenav');  
    const size = document.body.clientWidth;
    if(size<571){
      return
    }
    if(event.target.id==='close'){
      main.style.marginLeft='10px';
      main.style.width='auto';
      return side.style.width='0px';
    }
    if(side.style.width===""){

      return side.style.width='300px';
    }
    if(side.style.width!=='0px'){
      main.style.marginLeft='10px';
      main.style.width='auto';
      return side.style.width='0px';  
    }
    return side.style.width='300px';
  }
  changeField=(event)=>{
    
    return this.setState({searchField:event.target.value}) 
  }


  renderSwitch(parameter){
    switch (parameter){
      case 'card':
        return <Main searchField={this.state.searchField}/>;
      case 'ofirmie':
        return <Ofirmie/>;
      case 'projekty':
        return <ProjektyIndywidualne/>;
      case 'wspolpraca':
        return <Wspolpraca />;
      default:
        return <Main searchField={this.state.searchField}/>;
    }
  }
  onButtonClick=(event)=>{
    
    switch(event.target.id){
      case 'baner-home':
        return this.setState({whatToRender:'card'});
      case 'baner-ofirmie':
        return this.setState({whatToRender:'ofirmie'});
      case 'baner-oferta':
        return this.setState({whatToRender:'card'});
      case 'baner-projekty-indywidualne':
        return this.setState({whatToRender:'projekty'});
      case 'baner-wspolpraca':
        return this.setState({whatToRender:'wspolpraca'});
      default:
        return this.setState({whatToRender:'card'});
    }
  }

      
  

  render() {
    {document.body.onresize=this.changeWidth}
    return (
      <div className="App">
        <Baner onchange={this.changeField} buttonClick={this.onButtonClick}/>
        <Scroll>
          <SideNav props={this.onclick} />
          {this.renderSwitch(this.state.whatToRender)}
          
        </Scroll>
      </div>
    );
  }
}

export default App;



