import React from 'react';

const Wspolpraca =()=>{
	return(
		<div className='mainComponent'>
			
				<div className="content_header" style={{width:'100%'}}>
						<h1>Wsppraca</h1>
				</div>

				<div className="wspolpraca">

					<p className="red">KONSULTACJE I OFERTY</p>

					<p><strong>IP Kontakt</strong> udziela klientom wszystkich podstawowych informacji technicznych, niezbdnych do wytypowania waciwego towaru. Na yczenie klienta skada oferty uwzgldniajce typ wyrobu wedug oznacze producenta, cen, warunki patnoci oraz termin i sposb dostawy. Zamwienie Klienta jest wice dla obu stron, z chwil wystawienia przez <strong>IP Kontakt</strong> jego pisemnego potwierdzenia lub dowodu sprzeday.</p>
					<p className="red">SKADANIE ZAMWIE</p>
					<p><b>IP Kontakt</b> realizuje zamwienia skadane faksem, e-mailem, telefonicznie, poczt oraz osobicie w biurze firmy.</p>
					<br />
					<span>Pierwsze zamwienie skadane w firmie powinno by przesane faksem i zawiera nastpujce informacje:</span>
					
					<ul>
						<li>Nazwa i adres firmy,</li>
						<li>Nr telefonu  do firmy,</li>
						<li>NIP,</li>
						<li>Nazwisko osoby skadajcej zamwienie,</li>

						<li>E-mail,</li>
						<li>Nr faksu,</li>
						<li>Numer katalogowy i nr producenta,</li>
						<li>Ilo zamawianego towaru,</li>
						<li>Dane do umieszczenia na fakturze,</li>
						<li>Adres dostarczenia towaru  jeeli jest inny, ni adres Zamawiajcego,</li>
						<li>Sposb transportu.</li>
					</ul>
					


					<span>Na yczenie Klienta <b>IP Kontakt</b> wysya poczt elektroniczn lub faksem potwierdzenie, ktre zawiera informacje o rodzaju i iloci zamwionego towaru, cenach oraz terminie realizacji.<br /><br />
					Firma <b>IP Kontakt</b> zastrzega sobie moliwo odmowy przyjcia zamwienia, jeeli klient zalega
					z patnociami lub jego sytuacja prawna lub finansowa moe powodowa problemy z dokonaniem patnoci w przyszoci.<br /><br />
					<span className="red">WARUNKI PATNOCI</span><br /></span>
					<span>Patno za towar regulowana jest na podstawie wystawionych przez <b>IP Kontakt</b> dokumentw sprzeday, ktrymi s faktury VAT uwzgldniajce warto towaru netto, VAT i ewentualne koszty transportu.<br /><br />
					Formy patnoci:</span>
					
					<ul>
						<li>gotwka przy odbiorze osobistym towaru z magazynu,</li>
						<li>pobranie przy odbiorze paczki,</li>
						<li>przelew bankowy.</li>
					</ul>
					<span>Po potwierdzeniu wiarygodnoci patniczej klienta moemy przyj warunki patnoci przelewem w terminie uzgodnionym. W przypadku przelewu wymagane jest terminowe regulowanie nalenoci, gdy w przeciwnym razie zastrzegamy sobie prawo do naliczenia odsetek karnych w ustawowej wysokoci. Realizacja nastpnego zamwienia bdzie wstrzymana do czasu uregulowania nalenoci za poprzednie dostawy.
					<br /><br />
					Numery naszych rachunkw bankowych:<br /><br />
					Alior Bank S.A.:<br /></span>
					<table style={{textAlign:'center'}} >
						<tbody>
							<tr><th>Waluta transakcji</th><th>Numer rachunku</th></tr>
							<tr><td>PLN</td><td>79 2490 0005 0000 4530 2293 3049</td></tr>
							<tr><td>EUR</td><td>27 2490 0005 0000 4600 4370 2126</td></tr>
							<tr><td>USD</td><td>12 2490 0005 0000 4600 5950 4763</td></tr>
						</tbody>
						</table>
					<span>
					<br /><br />
					<span className="red">WARUNKI DOSTAWY</span><br />
					Sprzeda odbywa si w formie wysykowej i na terenie firmy. Umoliwiamy Pastwu wybr formy odbioru towaru. Towar mona odbiera zarwno za porednictwem firm spedycyjnych oraz poczty jak i osobicie. Korzystamy z nastpujcych firm spedycyjnych: UPS, DPD, DHL, Raben, Rhenus, i innych.<br /><br />
					<span className="red">CZAS DOSTAWY</span><br /></span>
					<span><b>IP Kontakt</b> prowadzi sprzeda towarw bezporednio z magazynu. Przyjmujemy rwnie zamwienia na towary nieobecne w ofercie magazynowej. Czas dostawy okrelany jest przez producenta i zaley od cyklu produkcyjnego towaru oraz czasu transportu.<br /><br />
					<span className="red">EKSPORT</span><br /><br /></span>
					<span className="red">KRAJE UNII EUROPEJSKIEJ</span><br />
					<span>W zwizku z wejciem Polski do Unii Europejskiej, wszystkich klientw majcych siedzib w innych krajach nalecych do Unii, prosimy o podanie nam swojego europejskiego numeru ewidencyjnego VAT. Umoliwi to dokonanie wewntrz-unijnej dostawy z zerow stawk VAT. Jeeli nie otrzymamy od Pastwa takiego potwierdzonego numeru bdziemy zmuszeni wystawi faktur obcion polsk stawk VAT, obecnie 23%.<br /><br />
					<span className="red">KRAJE SPOZA UNII EUROPEJSKIEJ</span><br />
					<span>Sprzedae eksportowe s dokonywane na podstawie dokumentu SAD, ktry naley potwierdzi na granicy. Jeeli w cigu miesica nie otrzymamy potwierdzonej na granicy deklaracji SAD, bdziemy musieli obciy klienta poniesionymi przez nas karami i podatkiem VAT.<br /><br />					<span className="red">PRZENIESIENIE WASNOCI<br /></span>
					Dostarczony towar staje si wasnoci Klienta po dokonaniu penej zapaty za towar. Do chwili otrzymania cakowitej zapaty towar pozostaje wasnoci <b>IP Kontakt</b>,  dlatego nie moe by przedmiotem zastawu, ani innego obcienia. W przypadku nie zapacenia caoci lub czci nalenej kwoty po ustalonym terminie <b>IP Kontakt</b> zastrzega sobie prawo odzyskania caego dostarczonego towaru.<br /><br />
					<span className="red">GWARANCJE I REKLAMACJE</span>
					</span><br />
					<b>IP Kontakt</b> odpowiada za wszystkie dostarczone towary, w tym za towary, ktre okazay si wadliwe (ze wzgldu na wady materiaowe lub bdy wykonawcze) w okresie zgodnym z gwarancj producenta, pod warunkiem, e byy stosowane w granicach parametrw specyfikacji technicznej i nie byy poddane naraeniom przekraczajcym te specyfikacje. Odpowiedzialno i zobowizania <b>IP Kontakt</b> ograniczaj si do wykonania bezpatnej wymiany. Reklamacje powinny by przesane przez Klienta w formie pisemnej w cigu piciu dni roboczych po otrzymaniu towaru lub natychmiast po ujawnieniu ukrytych wad. 
					<br /><br />
					<b>IP Kontakt</b> stale poszerza i rozwija asortyment produktw aby sprosta wymaganiom klientw. Wiele pozycji dostpnych jest w ofercie magazynowej.
					</span>
				</div>
			
		</div>
		)}
export default Wspolpraca;


