import React from 'react';

const Ofirmie = () =>{
	return(
		<div className='mainComponent'>
			
				<div className="content_header" style={{width:'100%'}}>
					<h1>O Firmie</h1>
				</div>
				<div id='ofirmieComponent'>
				
					<p>Firma <strong>IP Kontakt</strong> oferuje bogaty wybr elementw montaowych i komponentw elektronicznych z tworzyw sztucznych i metali dla rnych gazi przemysu.</p>
					<p>W ofercie handlowej IP Kontakt mog Pastwo znale elementy montaowe, osprzt owietleniowy i komponenty elektroniczne renomowanych wiatowych producentw sprowadzane z Europy Zachodniej, USA i Dalekiego Wschodu.  Midzy innymi oferujemy szerok gam produktw firmy <strong>Richco</strong>.</p>
					<p>Oferowane przez nas produkty wytwarzane s wedug standardw wiatowych i unijnych oraz norm ISO9001 i ISO 14001. Speniaj rwnie wymogi Unijnej Dyrektywy RoHS. Informacje dotyczce zgodnoci z dyrektyw RoHS podajemy w oparciu o dane uzyskane od naszych dostawcw.</p>
					<p>Firma <strong>IP Kontakt</strong> zajmuje si dystrybucj krajow i eksportem. Naszymi klientami s zakady produkcyjne oraz hurtownie. Zapewniamy cigo i terminowo dostaw. Proponujemy atrakcyjne warunki wsppracy, pomoc techniczn i logistyczn, bezpatne prbki oraz mi obsug. IP Kontakt stale poszerza i rozwija asortyment produktw aby sprosta wymaganiom klientw. Wiele pozycji dostpnych jest w ofercie magazynowej.</p>
					<p><a>Polityka prywatnoci</a></p>
			
				
				</div>	
		</div>
		)}
export default Ofirmie


