import React from 'react';
import Card from './card.js';
import mainComponentObjects from './mainComponentObjects.js';

const Main =({searchField})=>{
	
	const cardArray=mainComponentObjects.map((obj,i)=>{
		return(
			<Card key={i} index={i} name={obj.name} />
			)

	});
	const adjustCardArray=cardArray.filter(card=>{
		
			return card.props.name.toLowerCase().includes(searchField.toLowerCase())
		}
		);

	
	return(
		<div className='mainComponent'>
			<div className='content_header' style={{width:'100%'}}>
				<h3>CZCI Z TWORZYW SZTUCZNYCH I METALU ORAZ KOMPONENTY ELEKTRONICZNE</h3>
			</div>
			{adjustCardArray}	
		</div>
		)
			
}

export default Main


